import React from 'react';
import { getColor } from '../utils/getColor';

const CardTypeFrameSVG = ({ x, y, cardType, civilizations }) => {
    const isEvolutionType = ['', 'V', 'GV', '', ''].includes(cardType);
    const displayType = isEvolutionType ? '' : cardType;

    const textLength = displayType.length;
    const width = textLength * 8.4 + 20; // 

    const gradientId = `card-type-gradient-${civilizations.join('-')}`;

    const getGradientStops = () => {
        const civCount = civilizations.length;
        const stops = [];

        // 0%
        stops.push(
            <stop
                key={`stop-0`}
                offset="-5%"
                stopColor={getColor(civilizations[0])}
            />
        );

        for (let i = 1; i < civCount; i++) {
            const offset = (i / civCount) * 100;

            // 
            stops.push(
                <stop
                    key={`stop-${i}-prev`}
                    offset={`${offset}%`}
                    stopColor={getColor(civilizations[i - 1])}
                />
            );

            // 
            stops.push(
                <stop
                    key={`stop-${i}-curr`}
                    offset={`${offset}%`}
                    stopColor={getColor(civilizations[i])}
                />
            );
        }

        // 100%
        stops.push(
            <stop
                key={`stop-end`}
                offset="100%"
                stopColor={getColor(civilizations[civCount - 1])}
            />
        );

        return stops;
    };

    return (
        <g transform={`translate(${x}, ${y})`}>
            <defs>
                <linearGradient
                    id={gradientId}
                    gradientTransform="rotate(-6)"
                    spreadMethod="pad"
                >
                    {getGradientStops()}
                </linearGradient>
            </defs>
            <rect
                x="0"
                y="0"
                width={width}
                height="15"
                fill={`url(#${gradientId})`}
                stroke="black"
                strokeWidth="1"
                rx="7.5"
            />
            <text
                x={width / 2}
                y="11"
                fontSize="8"
                fontWeight="bold"
                fill="black"
                textAnchor="middle"
                fontFamily="LineSeedJP"
            >
                {displayType}
            </text>
        </g>
    );
};

export default CardTypeFrameSVG;
