import React from 'react';
import { getColor } from '../utils/getColor';

const ManaCircleSVG = ({ x, y, civilizations }) => {
    const circleRadius = 7; // 
    const circleCircumference = 2 * Math.PI * circleRadius; // 

    const getStrokeDashArray = () => {
        const civCount = civilizations.length;
        const dashLength = circleCircumference / civCount; // 
        return `${dashLength} ${circleCircumference - dashLength}`;
    };

    const getCivilizationSegments = () => {
        const civCount = civilizations.length;
        const dashLength = circleCircumference / civCount;

        // 45-90
        const rotationAngle = civCount % 2 === 0 ? 45 : -90;

        return civilizations.map((civ, index) => {
            const offset = -index * dashLength; // 
            return (
                <circle
                    key={civ}
                    cx="17.5" // X
                    cy="17.5" // Y
                    r={circleRadius} // 
                    fill="none" // 
                    stroke={getColor(civ)} // 
                    strokeWidth="15" // 
                    strokeDasharray={getStrokeDashArray()} // 
                    strokeDashoffset={offset} // 
                    transform={`rotate(${rotationAngle} 17.5 17.5)`} // 
                />
            );
        });
    };

    // 501
    const manaNumber = civilizations.length === 5 ? '0' : '1';

    return (
        <g transform={`translate(${x}, ${y})`}>
            {/* 12 */}
            <circle
                cx="17.5"
                cy="17.5"
                r={circleRadius}
                fill="none"
                stroke="black"
                strokeWidth={civilizations.length >= 2 ? "23.5" : "16.5"} // 1
            />
            {/* 2 */}
            {civilizations.length >= 2 && (
                <>
                    {/*  */}
                    <defs>
                        <path
                            id="manaCirclePath"
                            d={`
                                M ${17.5 - (circleRadius + 3)} ${17.5} 
                                A ${circleRadius + 3} ${circleRadius + 3} 0 1 1 ${17.5 + (circleRadius + 3)} ${17.5}
                            `}
                        />
                    </defs>
                    {/*  */}
                    <text
                        fontSize="3.3"
                        fill="white"
                        fontFamily="LINESeed"
                        dy="-5" // 
                    >
                        <textPath
                            href="#manaCirclePath"
                            startOffset="0%"
                        >
                            
                        </textPath>
                    </text>
                </>
            )}
            {/*  */}
            {getCivilizationSegments()}
            {/* 180 */}
            {/* 180 */}
            {/*  */}
            <text
                x="17.5"
                y="22"
                transform="rotate(180 17.5 17.5)"
                fontSize="16"
                fontWeight="bold"
                stroke="black"
                strokeWidth="2"
                fill="none" // 
                textAnchor="middle"
                fontFamily="LineSeedJP"
            >
                {manaNumber}
            </text>
            {/*  */}
            <text
                x="17.5"
                y="22"
                transform="rotate(180 17.5 17.5)"
                fontSize="16"
                fontWeight="bold"
                fill="white"
                textAnchor="middle"
                fontFamily="LineSeedJP"
            >
                {manaNumber}
            </text>
        </g>
    );
};

export default ManaCircleSVG;
