import React from 'react';
import EvoFramePart from '../assets/parts/evo.svg';
import EvoGrayFramePart from '../assets/parts/evoGray.svg';
import EvoUltraFramePart from '../assets/parts/evoUltra.svg';
import EvoStarFramePart from '../assets/parts/evoStar.svg';
import EvoStarMAXFramePart from '../assets/parts/evoStarMax.svg';

const FramePartsSVG = ({ cardType }) => {
    const evolutionTypes = {
        '': { count: 1, img: EvoFramePart },
        'V': { count: 2, img: EvoFramePart },
        'GV': { count: 3, img: EvoFramePart },
        '': { count: 4, img: EvoFramePart, lastImg: EvoGrayFramePart },
        '': { count: 1, img: EvoUltraFramePart },
        '': { count: 1, img: EvoStarFramePart },
        'S-MAX': { count: 1, img: EvoStarMAXFramePart }
    };

    const evoData = evolutionTypes[cardType];

    if (!evoData) return null;

    const { count, img, lastImg } = evoData;
    const elements = [];

    for (let i = count; i > 0; i--) {
        const isLast = i === count && lastImg;
        // 4
        const yPosition = count < 4 ? 170 - i * 25 + 20 : 170 - i * 10 + 3;   // 4

        elements.push(
            <image
                key={i}
                href={isLast ? lastImg : img}
                x={5}
                y={yPosition}
                width={30}
                height={30}
            />
        );
    }

    return <g>{elements}</g>;
};

export default FramePartsSVG;
