import React from 'react';

const CardNameSVG = ({
    x,
    y,
    cardName,
    rubyText,
    race,
    cardNameFontSize,
    rubyTextFontSize,
}) => {
    const calculatedFontSize =
        cardNameFontSize ||
        (cardName.length <= 8
            ? 21
            : cardName.length <= 14
                ? 14.5
                : Math.max(10, 14 - (cardName.length - 14)));

    return (
        <g transform={`translate(${x}, ${y})`} textAnchor="middle">
            {/*  */}
            {rubyText && (
                <text
                    x="0"
                    y="0"
                    fontSize={rubyTextFontSize || 6}
                    fontWeight="bold"
                    fill="white"
                    stroke="black"
                    strokeWidth="0.35"
                    fontFamily="LineSeedJP"
                >
                    {rubyText}
                </text>
            )}
            {/*  */}
            <text
                x="0"
                y="13.5"
                fontSize={calculatedFontSize}
                fontWeight="bold"
                fill="white"
                stroke="black"
                strokeWidth="0.7"
                fontFamily="LineSeedJP"
            >
                {cardName}
            </text>

            {/*  */}
            {race && (
                <text
                    x="0"
                    y="25"
                    fontSize="7"
                    fontWeight="bold"
                    fill="white"
                    stroke="black"
                    strokeWidth="0.5"
                    fontFamily="LineSeedJP"
                >
                    {race}
                </text>
            )}
        </g>
    );
};

export default CardNameSVG;
