import React, { useState, useRef } from 'react';
import { TextField, Slider, Checkbox, FormControlLabel, FormGroup, Button } from '@mui/material';
import { civilizations } from '../utils/CardProperties';
import CardFrameSVG from './CardFrameSVG';
import ImageUploadAndCrop from './ImageUploadAndCrop';
import { toPng } from 'html-to-image';
import './CardFormWithSVGDownload.css';

const CardFormWithSVGDownload = () => {
    // 
    const [formData, setFormData] = useState({
        cardName: '',
        civilization: [],
        cost: 0,
        power: 0,
        race: '',
        cardType: '',
        effectText: '',
        flavorText: '',
        rubyText: '',
        cardNameFontSize: 18,
        effectTextFontSize: 12,
        flavorTextFontSize: 6,
        rubyTextFontSize: 8,
        raceFontSize: 7, // 
    });

    const [imageUrl, setImageUrl] = useState(null); // URL
    const cardRef = useRef(null); // 

    // 
    const handleCivilizationChange = (e) => {
        const { value, checked } = e.target;
        setFormData((prevState) => {
            const selectedCivilizations = prevState.civilization;
            if (checked) {
                return {
                    ...prevState,
                    civilization: [...selectedCivilizations, value],
                };
            } else {
                return {
                    ...prevState,
                    civilization: selectedCivilizations.filter((civ) => civ !== value),
                };
            }
        });
    };

    // 
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    // 
    const handleSliderChange = (e, value) => {
        const name = e.target.getAttribute('name');
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    // 
    const handleImageUpload = (croppedImage) => {
        setImageUrl(croppedImage);
    };

    // PNG
    const downloadCardAsPNG = () => {
        if (cardRef.current) {
            const cardElement = cardRef.current;

            toPng(cardElement, {
                filter: (node) => node.tagName !== 'button' && node.tagName !== 'form',
                quality: 1,
                width: cardElement.clientWidth,
                height: cardElement.clientHeight,
                style: {
                    backgroundColor: 'transparent',
                },
            })
                .then((dataUrl) => {
                    const link = document.createElement('a');
                    link.download = `${formData.cardName || 'card'}.png`;
                    link.href = dataUrl;
                    link.click();
                })
                .catch((error) => {
                    console.error('PNG', error);
                });
        }
    };

    return (
        <div className="card-form-container">
            <form className="card-form">
                {/*  */}
                <TextField
                    label=""
                    name="cardName"
                    value={formData.cardName}
                    onChange={handleChange}
                    fullWidth
                    margin="normal"
                />

                {/*  */}
                <div>
                    <label>: {formData.cardNameFontSize}px</label>
                    <Slider
                        name="cardNameFontSize"
                        min={10}
                        max={30}
                        value={formData.cardNameFontSize}
                        onChange={(e, value) => handleSliderChange(e, value)}
                        aria-labelledby="card-name-font-size-slider"
                    />
                </div>

                {/*  */}
                <TextField
                    label=""
                    name="rubyText"
                    value={formData.rubyText}
                    onChange={handleChange}
                    fullWidth
                    margin="normal"
                />

                {/*  */}
                <div>
                    <label>: {formData.rubyTextFontSize}px</label>
                    <Slider
                        name="rubyTextFontSize"
                        min={6}
                        max={14}
                        value={formData.rubyTextFontSize}
                        onChange={(e, value) => handleSliderChange(e, value)}
                        aria-labelledby="ruby-text-font-size-slider"
                    />
                </div>

                {/*  */}
                <TextField
                    label=""
                    name="race"
                    value={formData.race}
                    onChange={handleChange}
                    fullWidth
                    margin="normal"
                />

                {/*  */}
                <div>
                    <label>: {formData.raceFontSize}px</label>
                    <Slider
                        name="raceFontSize"
                        min={6}
                        max={14}
                        value={formData.raceFontSize}
                        onChange={(e, value) => handleSliderChange(e, value)}
                        aria-labelledby="race-font-size-slider"
                    />
                </div>

                {/*  */}
                <div>
                    <label></label>
                    <FormGroup row>
                        {civilizations.map((civ) => (
                            <FormControlLabel
                                key={civ.value}
                                control={
                                    <Checkbox
                                        value={civ.value}
                                        checked={formData.civilization.includes(civ.value)}
                                        onChange={handleCivilizationChange}
                                    />
                                }
                                label={civ.label}
                            />
                        ))}
                    </FormGroup>
                </div>

                {/*  */}
                <TextField
                    label=""
                    name="effectText"
                    value={formData.effectText}
                    onChange={handleChange}
                    fullWidth
                    margin="normal"
                    multiline
                    rows={6}
                />

                {/*  */}
                <div>
                    <label>: {formData.effectTextFontSize}px</label>
                    <Slider
                        name="effectTextFontSize"
                        min={8}
                        max={20}
                        value={formData.effectTextFontSize}
                        onChange={(e, value) => handleSliderChange(e, value)}
                        aria-labelledby="effect-text-font-size-slider"
                    />
                </div>

                {/*  */}
                <TextField
                    label=""
                    name="flavorText"
                    value={formData.flavorText}
                    onChange={handleChange}
                    fullWidth
                    margin="normal"
                    multiline
                    rows={4}
                />

                {/*  */}
                <div>
                    <label>: {formData.flavorTextFontSize}px</label>
                    <Slider
                        name="flavorTextFontSize"
                        min={6}
                        max={14}
                        value={formData.flavorTextFontSize}
                        onChange={(e, value) => handleSliderChange(e, value)}
                        aria-labelledby="flavor-text-font-size-slider"
                    />
                </div>
            </form>

            {/*  */}
            <div className="preview-container">
                <ImageUploadAndCrop onImageUpload={handleImageUpload} />
                {imageUrl && (
                    <>
                        <div ref={cardRef} className="card-preview-frame">
                            <CardFrameSVG
                                imageUrl={imageUrl}
                                cardName={formData.cardName}
                                civilization={formData.civilization}
                                cost={formData.cost}
                                power={formData.power}
                                race={formData.race}
                                cardType={formData.cardType}
                                effectText={formData.effectText}
                                flavorText={formData.flavorText}
                                rubyText={formData.rubyText}
                                cardNameFontSize={formData.cardNameFontSize}
                                effectTextFontSize={formData.effectTextFontSize}
                                flavorTextFontSize={formData.flavorTextFontSize}
                                rubyTextFontSize={formData.rubyTextFontSize}
                                raceFontSize={formData.raceFontSize}  // 
                            />
                        </div>
                        <Button className='download-button' variant="contained" color="primary" onClick={downloadCardAsPNG}>
                            PNG
                        </Button>
                    </>
                )}
            </div>
        </div>
    );
};

export default CardFormWithSVGDownload;
